body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Merriweather Sans', sans-serif;
  line-height: 1.4;
  color: #fff;
}

a {
  color: #333;
  text-decoration: none;
}

.App {
  display: flex;
  flex-direction: column;
}

.view {
  max-width: 100%;
  width: 100vw;
  height: 100vh;
}

h1 {
  font-size: 5rem;
}
.HomeView {

  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
  width: 100vw;
  height: 100vh;
}

.CoolBackGround {
  position: absolute;
  z-index: -1;
  width: 100%; 
  height: 100%;

  font-size: 4rem;
  line-height: 0.8;

  overflow: hidden;
}


.grid-container {
  display: grid;
  grid-template-columns: repeat( auto-fill, minmax(4rem, 2fr) );
  justify-content: center;
}
.HomeTitle {
  /* margin-top: 25vh; */
  /* width: 100%; */
  /* text-align: center; */
  text-align: right;  
  align-items: right;
  align-self: 'stretch';
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.Name {
  font-size: 5rem;
  text-align: right;
  line-height: 1.1;
}


.Blurb {
  font-style: italic;
}
